import s from './Contacts.module.css';
const Contacts = ({ list }) => {
  return (
    <section className={s.contacts}>
      <h2 className={`${s.h1} ${s.h2}`}>Contacts</h2>
      <ul className={s.list}>
        {list.map(item => {
          console.log('элемент  ---',item);
        return(
          <li
            className={s.item}
            key={item.id}>
            <p className={s.stringElem}>{item.name}:</p> 
            <p>{item.number}</p>
          </li>
        )})}
      </ul>
    </section>
  );
};

export default Contacts;
